//            this.$store.dispatch('resetUploadedImages');
//            this.$router.push('/home');
//          });
//        }
      }
    }
  };

</script>

<style lang="scss">
  @import "../styles/colors.scss";

  .searchbox {
    background-color: $c-humming-bird;
    border-radius: 6px;
    padding: 10px;
    margin-top: 10px;
  }

</style>



